/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Arial, sans-serif;
  
}

/* Body Background - only works if added globally, not in module */
/* Consider placing this in global styles.css if necessary */

/* Status Page Container */
.statusContainer {
  max-width: 900px;
  margin: 30px auto;
  padding: 40px 50px;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 2px 8px black;
  margin-top: 100px;
}

.heading {
  color: black;
  text-align: left;
  margin-bottom: 15px;
  font-size: 25px;
}

/* Status Table */
.statusTable {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 12px;
  background-color: transparent;
}

.statusTable th {
  background-color: rgba(37, 99, 161, 0.9);
  color: white;
  font-weight: bold;
  padding: 12px;
  font-size: 12px;
  text-align: left;
}

.statusTable th:first-child {
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
}

.statusTable th:last-child {
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
}

.statusTable td {
  padding: 10px;
  background-color: rgba(221, 207, 207, 0.5);
  font-size: 12px;
  color: #333;
}

.statusTable tr:hover td {
  background-color: rgba(240, 240, 240, 0.9);
}

/* Status Colors */
.status-approved {
  color: #2ecc71;
  font-weight: bold;
}

.status-rejected {
  color: #e74c3c;
  font-weight: bold;
}
